import { useTranslation } from "react-i18next";
import { useCallback, useState } from "react";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import {
  useAddStudentInPracticalBatchClasses,
  useOnePracticalBatchClasses,
} from "../../../../Hooks/ApiHooks/AcademicDepartmentManager/academic-department-manager-api";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import CreateButton from "../../../../Ui/Button/CreateButton";
import InstitutePracticalBatchClassDetailCard from "./InstitutePracticalBatchClassDetailCard";
import InstituteDepartmentAddStudent from "../InstituteDepartmentAddStudent/InstituteDepartmentAddStudent";
import InstitutePracticalBatchClassDetailReshuffle from "./InstitutePracticalBatchClassDetailReshuffle/InstitutePracticalBatchClassDetailReshuffle";
import { useLocation } from "react-router-dom";
import InstituteDepartmentAddDseStudent from "../InstituteDepartmentAddStudent/InstituteDepartmentAddDseStudent";

const InstitutePracticalBatchClassDetail = ({ clsId, openAs }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [search, setSearch] = useState("");
  const [add, setAdd] = useState(false);
  const [dse, setDse] = useState(false);

  const [reshuffle, setReshuffle] = useState(false);
  const [disabled, setDisabled] = useState(false);

  const [addStudentInPracticalBatchClasses] =
    useAddStudentInPracticalBatchClasses();
  const {
    onePracticalBatchClasses,
    onePracticalBatchClassesLoading,
    onePracticalBatchClassesRefetch,
  } = useOnePracticalBatchClasses({
    data: {
      sid: getQuery.state.subId,
      search: search,
    },
    skip: !clsId,
  });

  const onDebounce = useCallback(
    customDebounce((val) => {
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setSearch("");
    }
  };

  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  const onDseToggle = () => {
    setDse((pre) => !pre);
  };

  const onReshuffleToggle = () => {
    setReshuffle((pre) => !pre);
  };
  const onAddStudent = (data) => {
    if (data?.length > 0 && getQuery.state.subId) {
      setDisabled((pre) => !pre);
      addStudentInPracticalBatchClasses({
        sid: getQuery.state.subId,
        bodyParameter: {
          students: data,
        },
      })
        .then(() => {
          onePracticalBatchClassesRefetch();
          setDisabled((pre) => !pre);
          onToggle();
        })
        .catch({});
    }
  };
  return (
    <>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>{getQuery.state?.subjectName ?? ""}</h6>
          <div className={style.fm_search}>
            <section className={style.fm_search_container}>
              <div className={style.fm_search_container_inner}>
                <img
                  className={style.fm_search_icon}
                  alt="search icon"
                  src={`${assestsNavbarUrl}/navbar-search.svg`}
                />
                <input
                  type="text"
                  placeholder={t("search")}
                  onChange={onSearchEvent}
                  style={{
                    minWidth: "200px",
                  }}
                />
              </div>
            </section>
            <CreateButton
              label={t("resuffle")}
              customStyle={{
                margin: "0",
                padding: "0.4rem 1rem",
                minWidth: "fit-content",
              }}
              onAction={onReshuffleToggle}
            />
            <CreateButton
              label={t("add_student")}
              customStyle={{
                margin: "0",
                padding: "0.4rem 1rem",
                minWidth: "fit-content",
              }}
              onAction={onToggle}
            />
            <CreateButton
              label={t("add_dse_student")}
              customStyle={{
                margin: "0",
                padding: "0.4rem 1rem",
                minWidth: "fit-content",
              }}
              onAction={onDseToggle}
            />
          </div>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0.3rem",
          paddingBottom: "1rem",
        }}
      >
        {onePracticalBatchClasses?.theory_students?.map((student) => (
          <InstitutePracticalBatchClassDetailCard
            student={student}
            key={student?._id}
            openAs={openAs}
            subjectId={getQuery.state.subId}
            onRefetch={onePracticalBatchClassesRefetch}
          />
        ))}
        {onePracticalBatchClassesLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <Loading />
          )
        ) : search ? (
          onePracticalBatchClasses?.theory_students?.length === 0 && (
            <EmptyMessage title="No student found related this search." />
          )
        ) : (
          onePracticalBatchClasses?.theory_students?.length === 0 && (
            <EmptyMessage title="No student." />
          )
        )}
      </UserMemberContentWrapper>

      {add && (
        <InstituteDepartmentAddStudent
          onClose={onToggle}
          clsId={clsId}
          onAdd={onAddStudent}
          academicDepartmentId={getQuery.state?.departmentId}
        />
      )}
      {dse && (
        <InstituteDepartmentAddDseStudent
          onClose={onDseToggle}
          clsId={clsId}
          onAdd={onAddStudent}
          academicDepartmentId={getQuery.state?.departmentId}
        />
      )}
      {reshuffle && (
        <InstitutePracticalBatchClassDetailReshuffle
          onClose={onReshuffleToggle}
          clsId={clsId}
          onRefetch={onePracticalBatchClassesRefetch}
        />
      )}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default InstitutePracticalBatchClassDetail;
